.WhatToWear {
    overflow: hidden;
    margin-top: 2rem;
    margin-bottom: 2rem;
    display: inline-block;

    &__rain-warning {
        display: inline-block;
    }

    &__rain-warning__icon {
        display: inline-grid;
        width: 100%;
        height: auto;
        position: relative;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr;

        &__warning {
            grid-column: 1;
        }
        &__umbrella {
            grid-column: 4;
        }
        &__raincoat {
            grid-column: 5;
        }
    }

    &__rain-warning__text {
        &__rain-msg {
            display: inline;
            text-transform: uppercase;
            font-size: 0.75rem;
            font-weight: 400;
        }
    }

    &__outfit {
        margin-top: 1rem;
        margin-bottom: 5rem;
        width: 100%;
        height: auto;
        position: relative;

        &__icons {
            display: inline-block;
        }
        &__suggestion-msg {
            text-transform: uppercase;
            font-weight: 300;
            letter-spacing: 0.5rem;
        }
    }
}
